body {
    margin: 0;
    padding: 0;
    font-family: -apple-system;
    background-color: darkgoldenrod;
    padding: 10px;
    font-family: Impact;
}

.App {
    background-color: white;
    max-width: 768px;
    margin: auto;
}

.title {
    text-align: center;
    background-color: black;
    color: darkgoldenrod;
    padding: 10px;
    box-shadow: 3px 3px 3px #333;
    width: 30%;
    margin-left: auto;
    margin-right: auto;
}

p {
    font-family: Lato;  
}
.Navbar {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background-color: hotpink;
    box-shadow: 3px 3px 3px #333;
}

@media only screen and (max-width : 768px){
    .Navbar {
        display: grid;
        grid-template-columns: 1fr;
    }

    .nav-menu {
        display: grid;
        grid-template-columns: 1fr;
        padding:0;
    }

    .nav-menu li a{
        display: block;
        text-align: center;
        margin: 2px;
    }
}

.nav-menu{
    list-style-type: none;
    margin-top: auto;
    margin-bottom: auto;
}

.logo-link a {
    color: black;
    background-color: darkgoldenrod;
    padding: 10px;
    border-radius: 2px;
    text-decoration: none;
    margin: 10px;
    box-shadow: 3px 3px 3px #333;
    transition: 1s;
}

.logo-link a:hover {
    color: darkgoldenrod;
    background-color: black;
    transition: 1s;
}

.nav-menu li {
    display: inline-block;
}

.nav-menu li a {
    text-decoration: none;
    background-color: white;
    padding: 10px;
    border-radius: 2px;
    margin-right: 5px;
    transition: 1s;
}

.nav-menu li a:hover {
    background-color: black;
    color: white;
    transition: 1s;
}

.home ul {list-style-type: none;}
.home img {box-shadow: 3px 3px 3px #333;}

.home .container {
    display: grid;
    position: relative;
    padding-top: 330px;
    padding-bottom: 20px;
    grid-template-columns: 1fr 1fr 1fr;
}

.home .defaultImage {
    position: absolute;
    z-index: 0;
    height: 60%;
    width: 70%;
    left: 15%;
}

.home .smallImage {
    width: 200px;
    height: 140px;
    transition: 1s;
}

.home .smallImage:hover {
    -webkit-transform: scale(.9);
            transform: scale(.9);
    opacity: .8;
    transition: 1s;
}

.home .bigImage {
    position: absolute;
    top: 0px;
    height: 60%;
    width: 70%;
    left: 15%;
    display: none;
}

.home li:hover .bigImage{
    display: block;
    z-index: 1;
}


@media only screen and (max-width: 768px){
    .container {
        grid-template-columns: 1fr;
        padding-left: 0;
        text-align: center;
        padding-top: 50vw;
    }

    .defaultImage, .bigImage {
        max-height: 45vw;
    }
}




